                   <p class="intro__box-text">–ò–Ω—Ç–µ—Ä–µ—Å –∫ –∏—Å—Ç–æ—Ä–∏–∏ –ø—Ä–æ–±—É–∂–¥–∞–ª—Å—è –ø–æ—Å—Ç–µ–ø–µ–Ω–Ω–æ, –∫–∞–∫ –∞–ø–ø–µ—Ç–∏—Ç —É —á–µ–ª–æ–≤–µ–∫–∞ (–∫–æ—Ç–æ—Ä—ã–π, –∫–∞–∫ –∏–∑–≤–µ—Å—Ç–Ω–æ, ¬´–ø—Ä–∏—Ö–æ–¥–∏—Ç –≤–æ –≤—Ä–µ–º—è –µ–¥—ã¬ª). –ù–∞—á–∞–ª–æ—Å—å –≤—Å—ë —Å–æ —à–∫–æ–ª—å–Ω—ã—Ö —É—Ä–æ–∫–æ–≤, —Å —Ä–∞—Å—Å–∫–∞–∑–æ–≤ —É—á–∏—Ç–µ–ª–µ–π –æ –¥–µ–ª–∞—Ö, –¥–∞–≤–Ω–æ –º–∏–Ω—É–≤—à–∏—Ö: –æ –ø—Ä–µ–¥–∞–Ω–Ω–æ—Å—Ç–∏ –∏ –ø—Ä–µ–¥–∞—Ç–µ–ª—å—Å—Ç–≤–∞—Ö, –æ –ø–æ–¥–≤–∏–≥–∞—Ö –∏ —Ç—Ä—É—Å–æ—Å—Ç–∏. –ö–∞–∫ —á–µ—Ä–Ω–æ–µ –∏ –±–µ–ª–æ–µ, —á–µ–ª–æ–≤–µ—á–µ—Å–∫–∏–µ –∫–∞—á–µ—Å—Ç–≤–∞ —Å—Ö–æ–¥–∏–ª–∏—Å—å –Ω–∞ –ø–æ–ª–æ—Ç–Ω–µ –∏—Å—Ç–æ—Ä–∏–∏ –∏ –æ–±—Ä–∞–∑–æ–≤—ã–≤–∞–ª–∏ —Ç–∞–º –Ω–µ–±—ã–≤–∞–ª–æ–µ –ø–µ—Ä–µ–ø–ª–µ—Ç–µ–Ω–∏–µ –∫—Ä–∞—Å–æ–∫. –ü–æ-–Ω–∞—Å—Ç–æ—è—â–µ–º—É —É–≤–ª–µ–∫ –º–µ–Ω—è –≤ –º–∏—Ä –∏—Å—Ç–æ—Ä–∏–∏ –º–æ–π –æ—Ç–µ—Ü, –∫–∞–Ω–¥–∏–¥–∞—Ç –∏—Å—Ç–æ—Ä–∏—á–µ—Å–∫–∏—Ö –Ω–∞—É–∫, –¥–æ—Ü–µ–Ω—Ç –í.–ù.–¢—Ä—É—Ö–∞—á–µ–≤. –ò—Ç–∞–∫, –∏—Å—Ç–æ—Ä–∏–∫ ‚Äì —ç—Ç–æ –∏—Å—Å–ª–µ–¥–æ–≤–∞—Ç–µ–ª—å, –∫–æ—Ç–æ—Ä—ã–π –¥–æ–ª–∂–µ–Ω –æ—Ç–¥–µ–ª–∏—Ç—å –ø—à–µ–Ω–∏—Ü—É –æ—Ç –ø–ª–µ–≤–µ–ª, –∞ –µ—Å–ª–∏ —ç—Ç–æ –Ω–µ–≤–æ–∑–º–æ–∂–Ω–æ, —Ç–æ —Ö–æ—Ç—è –±—ã –ø–æ–ø—ã—Ç–∞—Ç—å—Å—è. –£—á–∞—Å—å –Ω–∞ –∏—Å—Ç–æ—Ä–∏—á–µ—Å–∫–æ–º —Ñ–∞–∫—É–ª—å—Ç–µ—Ç–µ –¢–ì–ü–£, —è –≤–µ—Ä–Ω—É–ª—Å—è –≤ —Å–≤–æ—é —à–∫–æ–ª—É. –û–± —ç—Ç–æ–º —è –º–µ—á—Ç–∞–ª —Å –ø–æ—Å–ª–µ–¥–Ω–µ–≥–æ –∑–≤–æ–Ω–∫–∞. –ù–æ –≤ 2001 –≥–æ–¥—É —è —Å—Ç–∞–ª –ø—Ä–µ–ø–æ–¥–∞–≤–∞—Ç–µ–ª–µ–º –≤ –¢–∞—à–∫–µ–Ω—Ç—Å–∫–æ–º –ü–æ–ª–∏—Ç–µ—Ö–Ω–∏—á–µ—Å–∫–æ–º –ø—Ä–æ—Ñ–µ—Å—Å–∏–æ–Ω–∞–ª—å–Ω–æ–º –∫–æ–ª–ª–µ–¥–∂–µ. –†–∞–±–æ—Ç–∞ –ø—Ä–µ–ø–æ–¥–∞–≤–∞—Ç–µ–ª—è –æ–∫–∞–∑–∞–ª–∞—Å—å –Ω–µ —Ç–∞–∫–æ–π –ø—Ä–æ—Å—Ç–æ–π, –∫–∞–∫ –º–Ω–µ –ø—Ä–µ–¥—Å—Ç–∞–≤–ª—è–ª–æ—Å—å –≤ —à–∫–æ–ª—å–Ω—ã–µ –≥–æ–¥—ã. –°–ª–æ–≤–æ —Ä–∞–±–æ—Ç–∞ –∑–¥–µ—Å—å –Ω–µ —Å–æ–≤—Å–µ–º –ø–æ–¥—Ö–æ–¥–∏—Ç. –°–ª—É–∂–µ–Ω–∏–µ‚Ä¶ –ü—Ä–µ–ø–æ–¥–∞–≤–∞—Ç–µ–ª—å ‚Äì –≤ –Ω–µ–∫–æ—Ç–æ—Ä–æ–º —Ä–æ–¥–µ —Å–≤—è—â–µ–Ω–Ω–æ—Å–ª—É–∂–∏—Ç–µ–ª—å, –æ–Ω –ø–æ—Å—Ç–∞–≤–ª–µ–Ω –æ–±—â–µ—Å—Ç–≤–æ–º –≤—ã–ø–æ–ª–Ω—è—Ç—å –≤–∞–∂–Ω–µ–π—à—É—é —Ñ—É–Ω–∫—Ü–∏—é ‚Äì –ø–µ—Ä–µ–¥–∞–≤–∞—Ç—å –ø–æ–¥—Ä–∞—Å—Ç–∞—é—â–µ–º—É –ø–æ–∫–æ–ª–µ–Ω–∏—é –Ω–∞–∫–æ–ø–ª–µ–Ω–Ω—ã–π —á–µ–ª–æ–≤–µ—á–µ—Å—Ç–≤–æ–º –æ–ø—ã—Ç. –û—Ç —Ç–æ–≥–æ, –∫–∞–∫ –ø—Ä–µ–ø–æ–¥–∞–≤–∞—Ç–µ–ª—å –æ—Ç–Ω–æ—Å–∏—Ç—Å—è –∫ —Å–≤–æ–µ–º—É –∑–∞–Ω—è—Ç–∏—é, –∑–∞–≤–∏—Å–∏—Ç –æ—á–µ–Ω—å –º–Ω–æ–≥–æ–µ. –°–µ—Ä–¥—Ü–µ –∫–∞–∂–¥–æ–≥–æ —É—á–µ–Ω–∏–∫–∞ –æ—Ç–∫—Ä—ã–≤–∞–µ—Ç—Å—è –ø–æ-—Ä–∞–∑–Ω–æ–º—É. –ù–∞–¥–æ —Ç–æ–ª—å–∫–æ –Ω–∞–π—Ç–∏ –∫–ª—é—á–∏–∫. –° –≥—Ä—É–ø–ø–æ–π –Ω–∞–¥–æ –ø–æ–¥—Ä—É–∂–∏—Ç—å—Å—è, –≤–∏–¥–µ—Ç—å –≤ —É—á–∞—â–∏—Ö—Å—è –Ω–µ –æ–±—ä–µ–∫—Ç –≤–æ—Å–ø–∏—Ç–∞—Ç–µ–ª—å–Ω–æ–π –¥–µ—è—Ç–µ–ª—å–Ω–æ—Å—Ç–∏, –∞ —Ç–æ–≤–∞—Ä–∏—â–µ–π. –ü—Ä–µ–ø–æ–¥–∞–≤–∞—Ç–µ–ª—å, –ø–æ –º–æ–µ–º—É —Ç–≤–µ—Ä–¥–æ–º—É —É–±–µ–∂–¥–µ–Ω–∏—é, ‚Äì —ç—Ç–æ —Ç–æ—Ç —á–µ–ª–æ–≤–µ–∫, –≤ –∫–æ—Ç–æ—Ä–æ–º —É—á–∞—â–∏–π—Å—è –º–æ–∂–µ—Ç –æ–±—Ä–µ—Å—Ç–∏ –¥—Ä—É–≥–∞ –∏ –ø–æ–º–æ—â–Ω–∏–∫–∞, —Å–æ–≤–µ—Ç–Ω–∏–∫–∞. –ü—Ä–∏ —ç—Ç–æ–º, –±–µ–∑—É—Å–ª–æ–≤–Ω–æ, –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –¥–µ—Ä–∂–∞—Ç—å –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–Ω—É—é –¥–∏—Å—Ç–∞–Ω—Ü–∏—é, –∏–Ω–∞—á–µ —É—á–µ–±–Ω—ã–π –ø—Ä–æ—Ü–µ—Å—Å –æ—Å—Ç–∞–Ω–æ–≤–∏—Ç—Å—è –≤–æ–≤—Å–µ. –ó–∞–¥–∞—á–∞ –ø–µ–¥–∞–≥–æ–≥–∞ ‚Äì –¥–∞—Ç—å –ø—É—Ç—å –∫ –ø–æ–∑–Ω–∞–Ω–∏—é –¥–ª—è –≤—Å–µ—Ö –∏ –∫–∞–∂–¥–æ–≥–æ. –£—á–∞—â–∏–º—Å—è –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å –∏–Ω—Ç–µ—Ä–µ—Å–Ω–æ —É—á–∏—Ç—å—Å—è. –ü–æ—ç—Ç–æ–º—É —Ö–æ—Ä–æ—à–æ, –∫–æ–≥–¥–∞ –ø—Ä–µ–ø–æ–¥–∞–≤–∞—Ç–µ–ª—å –≤–∫–ª—é—á–∞–µ—Ç –≤ —É—á–µ–±–Ω—ã–π –ø—Ä–æ—Ü–µ—Å—Å —ç–ª–µ–º–µ–Ω—Ç—ã –∏–≥—Ä—ã –∏ —Å–æ—Å—Ç—è–∑–∞—Ç–µ–ª—å–Ω–æ—Å—Ç–∏. –¢–∞–∫–∂–µ —Ä–æ—Å—Ç—É –∏–Ω—Ç–µ—Ä–µ—Å–∞ –∫ –∏—Å—Ç–æ—Ä–∏–∏ —Å–ø–æ—Å–æ–±—Å—Ç–≤—É—é—Ç —Ä–∞–∑–ª–∏—á–Ω—ã–µ –º–µ—Ä–æ–ø—Ä–∏—è—Ç–∏—è –≤–æ –≤–Ω–µ—É—Ä–æ—á–Ω–æ–µ –≤—Ä–µ–º—è: –≤–∏–∫—Ç–æ—Ä–∏–Ω—ã, –∫–æ–Ω–∫—É—Ä—Å—ã, –ø–æ—Å–µ—â–µ–Ω–∏–µ –º—É–∑–µ–µ–≤, –ø—Ä–µ–¥—Å—Ç–∞–≤–ª–µ–Ω–∏—è, –ø—Ä–æ—Å–º–æ—Ç—Ä—ã –∏—Å—Ç–æ—Ä–∏—á–µ—Å–∫–∏—Ö —Ñ–∏–ª—å–º–æ–≤. –ú–Ω–µ –æ—á–µ–Ω—å –±–ª–∏–∑–∫–∞ –º—ã—Å–ª—å, —Å—Ñ–æ—Ä–º—É–ª–∏—Ä–æ–≤–∞–Ω–Ω–∞—è –¥–∞–≤–Ω–æ, –Ω–æ –æ—Ç —ç—Ç–æ–≥–æ –Ω–µ —Å—Ç–∞–≤—à–∞—è –º–µ–Ω–µ–µ –∞–∫—Ç—É–∞–ª—å–Ω–æ–π: –æ—Å–Ω–æ–≤–Ω–∞—è —Ü–µ–ª—å –¥–µ—è—Ç–µ–ª—å–Ω–æ—Å—Ç–∏ –ø—Ä–µ–ø–æ–¥–∞–≤–∞—Ç–µ–ª—è ‚Äì –Ω–µ ¬´–Ω–∞—É—á–∏—Ç—å¬ª, –∞ —Å–ø–æ—Å–æ–±—Å—Ç–≤–æ–≤–∞—Ç—å —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏—é —É –¥–µ—Ç–µ–π –Ω–∞–≤—ã–∫–æ–≤ —Å–∞–º–æ—Å—Ç–æ—è—Ç–µ–ª—å–Ω–æ–≥–æ –º—ã—à–ª–µ–Ω–∏—è –∏ –ø–æ–∏—Å–∫–∞. –ò–º–µ–Ω–Ω–æ —Ç–∞–∫–∏–µ –≥—Ä–∞–∂–¥–∞–Ω–µ –Ω—É–∂–Ω—ã –Ω–∞—à–µ–π —Å—Ç—Ä–∞–Ω–µ: —Ç–≤–æ—Ä—á–µ—Å–∫–∏–µ, –∞–∫—Ç–∏–≤–Ω—ã–µ, —Å–º–µ–ª—ã–µ. –ò –µ—Å–ª–∏ –≤ —ç—Ç–æ–º –±—É–¥—É—â–µ–º –±—É–¥–µ—Ç —á–∞—Å—Ç–∏—á–∫–∞ –º–æ–∏—Ö —Ç–≤–æ—Ä—á–µ—Å–∫–∏—Ö —É—Å–∏–ª–∏–π, —è –±—É–¥—É —Å—á–∏—Ç–∞—Ç—å —Å–≤–æ—é –¥–µ—è—Ç–µ–ª—å–Ω–æ—Å—Ç—å –Ω–µ –Ω–∞–ø—Ä–∞—Å–Ω–æ–π.</p> 
